import React from "react";
import base64url from "base64url";
import queryString from "query-string";

class GreetingCardFromURL extends React.Component {
  state = { decoded: {} };
  componentDidMount() {
    this.setState({
      decoded: JSON.parse(
        base64url.decode(queryString.parse(window.location.search).vcgc)
      )
    });
  }
  render() {
    return (
      <React.Fragment>
        <pre>{JSON.stringify(this.state.decoded, null, 2)}</pre>
        {this.state.decoded.greetingCard &&
          this.state.decoded.greetingCard.image && (
            <img alt="" src={this.state.decoded.greetingCard.image} />
          )}
      </React.Fragment>
    );
  }
}
export default GreetingCardFromURL;
